.region{
  width: 300px;
  .data-container{
    margin: 20px 0;
    text-align: center;
    border: 1px solid #333;
    border-radius: 5px;
    padding: 10px;
    button{
      width: 200px;
      padding: 13px 20px;
      background: $color-1;
      border: none;
      border-radius: 5px;
      margin: 10px 0;
      color: $color-3;
      cursor: pointer;
      &:hover{
        background: #3a7b90;
      }
    }
  }

}