.france-container{
  margin: 0 2rem;
   h1{
     text-align: center;
     margin-bottom: 30px;
     color: $color-3;
     font-weight: lighter;
   }
  .desc{
    text-align: center;
    color: $color-1;
    margin-bottom: 50px;
  }
  .data-content{
    padding-top: 30px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  @media screen and (max-width: 1165px) {
    .data-content {
      justify-content: center;
    }
  }
}

